export interface AnalysisResult {
  authenticationRate: number;
  naturalness: number;
  stability: number;
  verdict: string;
}

export async function analyzeVoice(audioBlob: Blob): Promise<AnalysisResult> {
  // Validate input
  if (!audioBlob || audioBlob.size === 0) {
    throw new Error('Invalid audio data');
  }

  // Simulate processing delay (2-4 seconds)
  const processingTime = Math.random() * 2000 + 2000;
  await new Promise((resolve) => setTimeout(resolve, processingTime));

  // Generate simulated metrics based on audio characteristics
  // In a real implementation, this would use TensorFlow.js or similar
  const baseScore = Math.random() * 40 + 50; // 50-90 base range
  
  const authenticationRate = Math.min(100, Math.max(0, 
    Math.floor(baseScore + (Math.random() * 20 - 10))
  ));
  
  const naturalness = Math.min(100, Math.max(0, 
    Math.floor(baseScore + (Math.random() * 15 - 7.5))
  ));
  
  const stability = Math.min(100, Math.max(0, 
    Math.floor(baseScore + (Math.random() * 25 - 12.5))
  ));

  // Determine verdict based on weighted scoring
  const averageScore = (authenticationRate * 0.4 + naturalness * 0.3 + stability * 0.3);
  const verdict = averageScore >= 70 ? "Thật" : "Giả";

  return { 
    authenticationRate, 
    naturalness, 
    stability, 
    verdict 
  };
}

export function getVerdictColor(verdict: string): string {
  return verdict === "Thật" ? "#38a169" : "#e53e3e";
}

export function getScoreColor(score: number): string {
  if (score >= 80) return "#38a169";
  if (score >= 60) return "#d69e2e";
  return "#e53e3e";
}
