"use client";
import { useState } from 'react';
import VoiceRecorder from '@/components/VoiceRecorder';
import AnalysisResult, { AnalysisResultData } from '@/components/AnalysisResult';
import { analyzeVoice } from '@/lib/voiceAnalysis';

export default function VoiceDetectionPage() {
  const [audioBlob, setAudioBlob] = useState<Blob | null>(null);
  const [analysis, setAnalysis] = useState<AnalysisResultData | null>(null);
  const [error, setError] = useState<string>('');
  const [isAnalyzing, setIsAnalyzing] = useState<boolean>(false);

  const handleRecordingComplete = async (blob: Blob) => {
    setAudioBlob(blob);
    setIsAnalyzing(true);
    setError('');
    
    try {
      const result = await analyzeVoice(blob);
      setAnalysis(result);
    } catch (err) {
      setError("Phân tích giọng nói gặp lỗi. Vui lòng thử lại.");
      console.error('Analysis error:', err);
    } finally {
      setIsAnalyzing(false);
    }
  };

  const handleReset = () => {
    setAudioBlob(null);
    setAnalysis(null);
    setError('');
    setIsAnalyzing(false);
  };

  const handleError = (errorMsg: string) => {
    setError(errorMsg);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 py-8 px-4">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-800 mb-4">
            Phát Hiện Giọng Nói Deepfake
          </h1>
          <p className="text-xl text-gray-600 max-w-2xl mx-auto">
            Sử dụng AI để phân tích và phát hiện giọng nói giả được tạo bởi công nghệ deepfake
          </p>
        </div>

        {/* Main Content */}
        <div className="space-y-8">
          {/* Recording Stage */}
          {!audioBlob && !isAnalyzing && !analysis && (
            <VoiceRecorder 
              onRecordingComplete={handleRecordingComplete} 
              onError={handleError} 
            />
          )}

          {/* Analysis Stage */}
          {(audioBlob || isAnalyzing) && !analysis && !error && (
            <div className="text-center bg-white rounded-lg shadow-lg p-12">
              <div className="mb-6">
                <div className="inline-flex items-center justify-center w-16 h-16 bg-blue-100 rounded-full mb-4">
                  <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin"></div>
                </div>
                <h2 className="text-2xl font-bold text-gray-800 mb-2">
                  Đang phân tích giọng nói...
                </h2>
                <p className="text-gray-600">
                  Vui lòng chờ trong giây lát, chúng tôi đang sử dụng AI để phân tích bản ghi âm của bạn
                </p>
              </div>
              
              <div className="max-w-md mx-auto">
                <div className="bg-gray-200 rounded-full h-2 mb-4">
                  <div className="bg-blue-600 h-2 rounded-full animate-pulse" style={{ width: '60%' }}></div>
                </div>
                <p className="text-sm text-gray-500">
                  Đang xử lý các đặc trưng âm thanh và so sánh với cơ sở dữ liệu...
                </p>
              </div>
            </div>
          )}

          {/* Results Stage */}
          {analysis && (
            <AnalysisResult data={analysis} onReset={handleReset} />
          )}

          {/* Error Stage */}
          {error && (
            <div className="text-center bg-white rounded-lg shadow-lg p-8">
              <div className="mb-6">
                <div className="inline-flex items-center justify-center w-16 h-16 bg-red-100 rounded-full mb-4">
                  <div className="w-8 h-8 text-red-600">⚠️</div>
                </div>
                <h2 className="text-2xl font-bold text-red-600 mb-2">
                  Có lỗi xảy ra
                </h2>
                <p className="text-gray-600 mb-6">
                  {error}
                </p>
                <button
                  onClick={handleReset}
                  className="px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition-colors duration-200"
                >
                  Thử lại
                </button>
              </div>
            </div>
          )}
        </div>

        {/* Info Section */}
        <div className="mt-16 bg-white rounded-lg shadow-lg p-8">
          <h2 className="text-2xl font-bold text-gray-800 mb-6 text-center">
            Cách thức hoạt động
          </h2>
          
          <div className="grid md:grid-cols-3 gap-6">
            <div className="text-center">
              <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-2xl">🎤</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2">1. Ghi âm</h3>
              <p className="text-gray-600 text-sm">
                Ghi lại giọng nói của bạn với chất lượng cao để phân tích
              </p>
            </div>
            
            <div className="text-center">
              <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-2xl">🤖</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2">2. Phân tích AI</h3>
              <p className="text-gray-600 text-sm">
                Sử dụng thuật toán AI để phân tích các đặc trưng giọng nói
              </p>
            </div>
            
            <div className="text-center">
              <div className="w-12 h-12 bg-purple-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-2xl">📊</span>
              </div>
              <h3 className="font-semibold text-gray-800 mb-2">3. Kết quả</h3>
              <p className="text-gray-600 text-sm">
                Nhận được báo cáo chi tiết về tính xác thực của giọng nói
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
