import React from 'react';
import { getVerdictColor, getScoreColor } from '@/lib/voiceAnalysis';

export interface AnalysisResultData {
  authenticationRate: number;
  naturalness: number;
  stability: number;
  verdict: string;
}

interface AnalysisResultProps {
  data: AnalysisResultData;
  onReset: () => void;
}

const AnalysisResult: React.FC<AnalysisResultProps> = ({ data, onReset }) => {
  const MetricCard = ({ title, value, description }: { title: string; value: number; description: string }) => (
    <div className="bg-gray-50 rounded-lg p-4 border border-gray-200">
      <div className="flex items-center justify-between mb-2">
        <h3 className="font-semibold text-gray-700">{title}</h3>
        <span 
          className="text-2xl font-bold"
          style={{ color: getScoreColor(value) }}
        >
          {value}%
        </span>
      </div>
      <div className="w-full bg-gray-200 rounded-full h-2 mb-2">
        <div 
          className="h-2 rounded-full transition-all duration-500"
          style={{ 
            width: `${value}%`,
            backgroundColor: getScoreColor(value)
          }}
        ></div>
      </div>
      <p className="text-sm text-gray-600">{description}</p>
    </div>
  );

  return (
    <div className="max-w-2xl mx-auto bg-white rounded-lg shadow-lg p-8">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-gray-800 mb-4">
          Kết quả phân tích
        </h2>
        
        {/* Final Verdict */}
        <div className="mb-6">
          <div className="text-lg text-gray-600 mb-2">Phán đoán cuối cùng:</div>
          <div 
            className="text-4xl font-bold px-6 py-3 rounded-lg inline-block"
            style={{ 
              color: getVerdictColor(data.verdict),
              backgroundColor: data.verdict === "Thật" ? "#f0fff4" : "#fef2f2",
              border: `2px solid ${getVerdictColor(data.verdict)}`
            }}
          >
            {data.verdict}
          </div>
        </div>
      </div>

      {/* Metrics Grid */}
      <div className="grid gap-6 mb-8">
        <MetricCard 
          title="Tỷ lệ xác thực"
          value={data.authenticationRate}
          description="Mức độ tin cậy của giọng nói dựa trên các đặc trưng sinh học"
        />
        
        <MetricCard 
          title="Mức độ tự nhiên"
          value={data.naturalness}
          description="Độ tự nhiên trong cách phát âm và ngữ điệu"
        />
        
        <MetricCard 
          title="Độ ổn định"
          value={data.stability}
          description="Tính nhất quán của các đặc trưng giọng nói trong suốt bản ghi"
        />
      </div>

      {/* Explanation */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-6">
        <h3 className="font-semibold text-blue-800 mb-2">Giải thích kết quả:</h3>
        <p className="text-blue-700 text-sm">
          {data.verdict === "Thật" 
            ? "Giọng nói của bạn có các đặc trưng tự nhiên của giọng nói con người thật. Các chỉ số đều ở mức cao, cho thấy đây là giọng nói tự nhiên."
            : "Phát hiện một số bất thường trong giọng nói có thể chỉ ra đây là giọng nói được tạo bởi AI. Các chỉ số thấp cho thấy có thể đây là deepfake."
          }
        </p>
      </div>

      {/* Action Buttons */}
      <div className="flex justify-center space-x-4">
        <button
          onClick={onReset}
          className="px-6 py-3 bg-gray-600 hover:bg-gray-700 text-white font-semibold rounded-lg transition-colors duration-200"
        >
          Ghi âm lại
        </button>
        
        <button
          onClick={() => {
            const results = {
              verdict: data.verdict,
              authenticationRate: data.authenticationRate,
              naturalness: data.naturalness,
              stability: data.stability,
              timestamp: new Date().toISOString()
            };
            
            const dataStr = JSON.stringify(results, null, 2);
            const dataBlob = new Blob([dataStr], { type: 'application/json' });
            const url = URL.createObjectURL(dataBlob);
            const link = document.createElement('a');
            link.href = url;
            link.download = `voice-analysis-${Date.now()}.json`;
            link.click();
            URL.revokeObjectURL(url);
          }}
          className="px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition-colors duration-200"
        >
          Tải kết quả
        </button>
      </div>
    </div>
  );
};

export default AnalysisResult;
